.Dots {
  position: absolute;
  -webkit-perspective: 300px;
          perspective: 300px; }
  .Dots div {
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #fff;
    float: left;
    opacity: 0;
    -webkit-box-shadow: 0 0 3px 1px rgba(255, 255, 255, 0.5);
            box-shadow: 0 0 3px 1px rgba(255, 255, 255, 0.5); }
