      height: 16px;
      cursor: pointer; }
      .NewWindow .TopBar .CloseBtn path {
        fill: #fff; }
      .NewWindow .TopBar .CloseBtn:hover path {
        fill: #DE4A4A; }
    .NewWindow .TopBar .NewWindowBtn {
