.GreetingContainer {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  position: relative; }

.Greeting {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  padding: 24px 0;
  overflow: hidden;
  color: #6d7eb5;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#6d7eb5), to(#6d7eb5)), -webkit-gradient(linear, left top, left bottom, from(#6d7eb5), to(#6d7eb5));
  background-image: -webkit-linear-gradient(#6d7eb5, #6d7eb5), -webkit-linear-gradient(#6d7eb5, #6d7eb5);
  background-image: -o-linear-gradient(#6d7eb5, #6d7eb5), -o-linear-gradient(#6d7eb5, #6d7eb5);
  background-image: linear-gradient(#6d7eb5, #6d7eb5), linear-gradient(#6d7eb5, #6d7eb5);
  background-size: 0px 3px, 0px 3px;
  background-repeat: no-repeat;
  background-position: top, bottom; }



